#news {
	width: 100%;
	height: 100vh;
	background: #fefbf4;
	position: relative;
	padding: 10vw;
	overflow: hidden;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	perspective: 700px;
	h1 {
		width: 100%;
		margin-bottom: 30px;
		font: bold 35px/1 $titleFont;
		position: relative;
		gap: 20px;
		z-index: 2;
	}

	.inner {
		width: 100%;
		height: 100%;
		margin: 0 auto;
		display: flex;
		flex-wrap: wrap;
		align-content: center;
		article {
			width: calc((100% - 60px) / 4);
			height: auto;
			margin-right: 20px;
			margin-bottom: 20px;
			// height: 200px;
			padding: 30px;
			// background: #fff;
			opacity: 0;
			transform: rotateY(180deg) translateY(100px);
			transition-duration: 0.5s;
			// border-radius: 10px;
			// box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
			border: 1px solid #333;
			// border: 1px solid #333;
			text-align: center;
			cursor: pointer;
			text-align: left;
			// &:nth-of-type(2n) {
			// 	transform: translateY(100px);
			// }
			&:last-child {
				margin-right: 0;
			}
			@for $i from 1 through 8 {
				&:nth-of-type(#{$i}) {
					transition-delay: 0.2s * ($i - 1);
				}
			}
			h1 {
				font: bold 50px/1 $contentFont;
				color: orange;
				margin-bottom: 100px;
			}
			h3 {
				padding-top: 20px;
				border-top: 1px solid #333;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 1;
				-webkit-box-orient: vertical;
			}
			p {
				margin-top: 30px;
				line-height: 1.5;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 5;
				-webkit-box-orient: vertical;
			}
			// &:hover {
			// 	transition: 0s;
			// 	@for $i from 1 through 8 {
			// 		&:nth-of-type(#{$i}) {
			// 			transition-delay: 0s;
			// 		}
			// 	}
			// 	background: rgba(0, 0, 0, 0.5);
			// 	color: #fff;
			// }
		}
	}
}
//활성화 모션
#news.on {
	article {
		opacity: 1;
		transform: rotateY(0deg) translateY(0px);
	}
}

@media screen and (max-width: $tablet) {
	#news {
		height: 100%;
		.inner {
			width: 100%;
			article {
				width: calc((100% - 40px) / 2);
				margin-right: 20px;
				transition-duration: 0;
				&:nth-of-type(2n) {
					margin-right: 0;
				}
				&:nth-of-type(3n) {
					margin-right: 20px;
				}
			}
		}
	}
}
@media screen and (max-width: $mobile) {
	#news {
		height: auto;
		h1 {
			margin-bottom: 50px;
			transform: none;
		}
		.inner {
			article {
				width: 100%;
				padding: 20px;
				margin-right: 0;
				margin-bottom: 30px;
				&:nth-of-type(3n) {
					margin-right: 0;
				}
			}
		}
	}
}
